import React, { Component } from "react";
import { connect } from "react-redux";
import { Row, Col, Container } from "react-bootstrap";

import {
    PlayBtn,
    mobileImg,
    PhoneFrame,
    Video,
} from "../../../Constants/Images";

// import { getSiteSettings } from "../../../Actions/getSettings";

import { YoutubeVideo, WantInModal } from "../../../Components/Modals";
import { validateEmail } from "../../../Utils/Helpers";
import Header from "../Header";
import Footer from "../Footer";

class Home extends Component {
    state = {
        email: "",
        emailError: false,
        youtubeModal: false,
        wantInModal: false,
        youtube_id: "aqz-KE-bpKQ",
    };

    componentDidMount = () => {
        this.fadeOutEffect();
        const { settings } = this.props;
        this.setState({
            youtube_id: settings.youtube_id,
        });
    };

    componentWillReceiveProps = (props) => {
        const { settings } = this.props;
        this.setState({
            youtube_id: settings.youtube_id,
        });
    };

    fadeOutEffect = () => {
        var fadeTarget = document.getElementById("loader");
        var fadeEffect = setInterval(function () {
            if (!fadeTarget.style.opacity) {
                fadeTarget.style.opacity = 1;
            }
            if (fadeTarget.style.opacity > 0) {
                fadeTarget.style.opacity -= 0.1;
            } else {
                clearInterval(fadeEffect);
            }
        }, 200);
    };

    onChange = (e) => {
        this.setState({
            email: e.target.value,
        });
    };

    submitEmail = () => {
        const { email } = this.state;
        const errors = {};
        if (!email || email === "") {
            errors["email"] = "Please enter your email.";
        } else if (!validateEmail(email)) {
            errors["email"] = "Please enter your valid email.";
        }

        console.log("Errors :", errors);

        if (Object.entries(errors).length !== 0) {
            this.setState({ emailError: true });
        } else {
            this.toggleModal("wantInModal", true);
        }
    };

    toggleModal = (modal, status) => {
        const state = this.state;
        state[modal] = status;
        this.setState({
            state,
        });
    };

    render() {
        const { email, emailError, youtubeModal, wantInModal, youtube_id } =
            this.state;
        return (
            <React.Fragment>
                <Header />
                <div className="page-wrapper">
                    <Container>
                        <Row className="align-items-center justify-content-between">
                            <Col md={5} sm={5} xs={12}>
                                <div className="left-side-text">
                                    <h3>Kings, Queens, and Giants</h3>
                                    <h2>ITS TIME TO BUILD</h2>
                                    <h4>
                                        The rebirth of Black Wall Street is here
                                    </h4>
                                    <p>
                                        We will no longer complain or ask others
                                        to change our circumstances. From this
                                        moment forward we gather, plot, and
                                        execute. We are royalty, even if others
                                        dont acknowledge it. Our community is
                                        now in our hands and pocket. We are all
                                        we got and all we need.
                                    </p>
                                    <p>
                                        This exclusive app was built by our
                                        community, for our community.
                                    </p>
                                    <div className="left-side-list">
                                        <h4>Get Ready To</h4>
                                        <ul>
                                            <li className="one">
                                                Unite as Kings, Queens, and
                                                Giants
                                            </li>
                                            <li className="two">
                                                Speak freely on topics important
                                                to you and the community
                                            </li>
                                            <li className="three">
                                                Abolish generational curses and
                                                keep the Black dollar closer
                                            </li>
                                        </ul>
                                    </div>
                                    <div className="want-in-form-wrapper">
                                        <span>
                                            * First 5,000 users will not require
                                            invite.
                                        </span>
                                        <div className="want-in-form">
                                            <input
                                                type="email"
                                                name="email-address"
                                                className={
                                                    emailError
                                                        ? "form-control error"
                                                        : "form-control"
                                                }
                                                onChange={(e) => {
                                                    this.onChange(e);
                                                }}
                                                value={email}
                                            />
                                            <button
                                                className="btn"
                                                onClick={() => {
                                                    this.submitEmail();
                                                }}
                                            >
                                                I WANT IN
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </Col>
                            <Col md={7} sm={7} xs={12}>
                                <Row className="d-flex align-items-center flex-row-reverse">
                                    <Col md={7} sm={7} xs={12}>
                                        <div className="mobile-wrapper">
                                            <img src={mobileImg} alt="" />
                                            <div className="video-wrapper">
                                                <video
                                                    id=""
                                                    className="animated_video"
                                                    width="370"
                                                    height="750"
                                                    autoPlay
                                                    loop
                                                    muted
                                                >
                                                    <source
                                                        src={Video}
                                                        type="video/mp4"
                                                    />
                                                    Your browser does not
                                                    support the video tag.
                                                </video>
                                                <img src={PhoneFrame} alt="" />
                                            </div>
                                        </div>
                                        <div className="mobile-wrapper-text">
                                            <p>
                                                *People in these profiles have
                                                not endorsed this app. Simply
                                                putting it in the universe.
                                            </p>
                                        </div>
                                    </Col>
                                    <Col md={4} sm={4} xs={12}>
                                        <div className="video-play-wrapper">
                                            <h2>
                                                This will become Wakanda in our
                                                pocket or the new Black
                                                Wallstreet
                                            </h2>
                                            <h3>
                                                - Vince from focus group session
                                            </h3>
                                            <div
                                                className="play-btn"
                                                onClick={() =>
                                                    this.toggleModal(
                                                        "youtubeModal",
                                                        true
                                                    )
                                                }
                                            >
                                                <img src={PlayBtn} alt="" />
                                            </div>
                                            <div
                                                className="learn-more-text as-link"
                                                onClick={() =>
                                                    this.toggleModal(
                                                        "youtubeModal",
                                                        true
                                                    )
                                                }
                                            >
                                                <span>Learn More</span>
                                            </div>
                                        </div>
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    </Container>
                    {youtubeModal && (
                        <YoutubeVideo
                            show={youtubeModal}
                            video={youtube_id}
                            toggleModal={() => {
                                this.toggleModal("youtubeModal", false);
                            }}
                        />
                    )}

                    {wantInModal && (
                        <WantInModal
                            show={wantInModal}
                            email={email}
                            toggleModal={() => {
                                this.toggleModal("wantInModal", false);
                            }}
                        />
                    )}
                </div>
                <Footer />
            </React.Fragment>
        );
    }
}

const mapStateProps = (state) => {
    return { settings: state.settings };
};

export default connect(mapStateProps, {})(Home);
