import React, { Component } from "react";
import YouTube from "react-youtube";

class YoutubeVideo extends Component {
    state = {
        showVideo: false,
        videoId: this.props.video,
    };

    componentWillMount = () => {
        setTimeout(() => {
            this.setState({
                showVideo: true,
            });
        });
    };

    _onReady(event) {
        // access to player in all event handlers via event.target
        event.target.pauseVideo();
    }

    closeModal = () => {
        const props = this.props;
        this.setState(
            {
                showVideo: false,
            },
            () => {
                setTimeout(() => {
                    props.toggleModal();
                }, 500);
            }
        );
    };

    render() {
        const { videoId, showVideo } = this.state;
        const opts = {
            height: "390",
            width: "640",
            playerVars: {
                autoplay: 1,
                controls: 0,
                rel: 0,
                showinfo: 0,
            },
        };

        return (
            <div className="videoModal ">
                <div className={`video ${showVideo ? "show" : ""}`}>
                    <span class="close" onClick={() => this.closeModal()}>
                        x
                    </span>
                    <YouTube
                        videoId={videoId}
                        opts={opts}
                        onReady={this._onReady}
                    />
                </div>
            </div>
        );
    }
}

export default YoutubeVideo;
