import axios from "axios";
import { BASE_URL } from "./env";

const postData = (url, data) => {
    return axios({
        method: "post",
        url: url,
        data,
        config: { headers: { "Content-Type": "multipart/form-data" } },
    }).then((res) => res.data);
};
// const postJsonData = (url, data) => {
//     return axios(url, data).then((res) => res.data);
// };
const getData = (url) => axios(url).then((res) => res.data);

const API = {
    submitFocusGroup: (data) =>
        postData(`${BASE_URL}/focus-group-request`, data),
    submitBecomePartner: (data) =>
        postData(`${BASE_URL}/become-partners-request`, data),
    submitWantInModal: (data) => postData(`${BASE_URL}/want-in-request`, data),
    submitDonateNow: (data) => postData(`${BASE_URL}/donate-now`, data),
    getSettings: () => getData(`${BASE_URL}/get-settings`),
};

export default API;
