import React, { Component } from "react";
import { Row, Col, Modal, Alert } from "react-bootstrap";
import { connect } from "react-redux";
import StripeCheckout from "react-stripe-checkout";

import API from "../../Utils/Api";

class DonateNow extends Component {
    state = {
        amount: 10,
        errors: {},
        submit: false,
        pk_stripe: "",
        errMsg: {
            show: false,
            type: "",
            message: "",
        },
    };

    componentWillMount = () => {
        const { settings } = this.props;
        this.setState({
            pk_stripe: settings.stripe_pk_key,
        });
    };

    componentWillReceiveProps = (props) => {
        const { settings } = props;
        this.setState({
            pk_stripe: settings.stripe_pk_key,
        });
    };

    onChange = (e) => {
        const temp = { ...this.state };
        temp[e.target.name] = e.target.value;

        this.setState(temp);
    };

    validateForm = () => {
        const { formData } = this.state;
        const errors = {};

        if (!formData.name) {
            errors["name"] = "Please enter your name";
        } else if (!formData.email) {
            errors["email"] = "Please enter your email";
        } else if (!formData.amount || formData.amount === 0) {
            errors["amount"] = "Please enter donation amount.";
        }

        return errors;
    };

    submitForm = (token) => {
        const { amount, submit } = this.state;

        if (!submit) {
            this.setState({ submit: true });

            console.log("Token:", token);

            const formData = new FormData();
            formData.append("token", JSON.stringify(token));
            formData.append("amount", amount);

            API.submitDonateNow(formData)
                .then((res) => {
                    this.setState({
                        submit: false,
                        errMsg: {
                            show: true,
                            type: res.type !== "error" ? res.type : "danger",
                            message: res.message,
                        },
                    });
                })
                .catch((err) => {
                    console.log(err);
                    this.setState({
                        submit: false,
                        errMsg: {
                            show: true,
                            type: "danger",
                            message: err.message,
                        },
                    });
                });
        }
    };

    closeModal = () => {
        this.setState(
            {
                formData: {},
            },
            () => {
                this.props.toggleModal(false);
            }
        );
    };

    hideErrMsg = () => {
        this.setState({
            errMsg: {
                show: false,
                type: "",
                message: "",
            },
        });
    };

    render() {
        const { errors, errMsg, amount, submit, pk_stripe } = this.state;
        const { show } = this.props;

        return (
            <>
                <Modal
                    className="small"
                    show={show}
                    onHide={() => this.closeModal()}
                >
                    <Modal.Header>
                        <span
                            className="close"
                            onClick={() => this.closeModal()}
                        >
                            x
                        </span>
                    </Modal.Header>
                    <Modal.Body>
                        <div className="modal-content-wrapper">
                            <h2>Donate</h2>
                            <p>Thanks for your support!</p>
                        </div>
                        <div className="form-wrapper">
                            {errMsg && errMsg.show && (
                                <Alert
                                    variant={errMsg.type}
                                    onClose={() => this.hideErrMsg()}
                                    dismissible
                                >
                                    {errMsg.message}
                                </Alert>
                            )}
                            <Row>
                                <Col>
                                    <div className="form-group">
                                        <label>Amount </label>
                                        <input
                                            type="number"
                                            name="amount"
                                            onChange={(e) => {
                                                this.onChange(e);
                                            }}
                                            className={
                                                errors.amount
                                                    ? "form-control error"
                                                    : "form-control"
                                            }
                                            value={amount}
                                        />
                                    </div>
                                </Col>
                            </Row>

                            <div className="form-group btn-wrapper">
                                {!submit && (
                                    <StripeCheckout
                                        stripeKey={pk_stripe}
                                        amount={amount * 100}
                                        token={this.submitForm}
                                    />
                                )}
                                {submit && (
                                    <button className="btn">
                                        <i className="fas fa-spinner fa-spin" />
                                    </button>
                                )}
                            </div>
                        </div>
                    </Modal.Body>
                </Modal>
            </>
        );
    }
}
const mapStateProps = (state) => ({
    settings: state.settings,
});
export default connect(mapStateProps, {})(DonateNow);
