import React, { Component } from "react";
import { Row, Col, Modal, Button, Alert } from "react-bootstrap";

import { validateEmail, appendFormData } from "../../Utils/Helpers";
import API from "../../Utils/Api";

class FocusGroup extends Component {
    state = {
        formData: {},
        errors: {},
        submit: false,
        errMsg: {
            show: false,
            type: "",
            message: "",
        },
    };

    componentDidMount = () => {};

    onChange = (e) => {
        const temp = { ...this.state.formData };
        temp[e.target.name] = e.target.value;

        this.setState({ formData: temp });
    };

    validateForm = () => {
        const { formData } = this.state;
        const errors = {};

        if (!formData.fname) {
            errors["fname"] = "Please enter your first name";
        } else if (!formData.lname) {
            errors["lname"] = "Please enter your last name";
        } else if (!formData.email) {
            errors["email"] = "Please enter your email";
        } else if (!validateEmail(formData.email)) {
            errors["email"] = "Please enter your valid email";
        } else if (!formData.phone) {
            errors["phone"] = "Please enter your phone number";
        } else if (formData.phone.length !== 10) {
            errors["phone"] = "Please enter your valid phone number";
        } else if (!formData.social_plateform) {
            errors["social_plateform"] = "Please select your social plateform";
        } else if (!formData.handle_url) {
            errors["handle_url"] = "Please enter your Handle or Url";
        }

        return errors;
    };

    submitForm = () => {
        const { submit, formData } = this.state;
        const errors = this.validateForm();

        if (Object.entries(errors).length === 0) {
            if (!submit) {
                this.setState({ submit: true });

                const data = appendFormData(formData);

                API.submitFocusGroup(data)
                    .then((res) => {
                        let newState = {
                            submit: false,
                            errMsg: {
                                show: true,
                                type:
                                    res.type === "error" ? "danger" : res.type,
                                message: res.message,
                            },
                        };
                        if (res.type === "success") {
                            newState = {
                                ...newState,
                                formData: {
                                    fname: "",
                                    lname: "",
                                    email: "",
                                    phone: "",
                                    social_plateform: "",
                                    handle_url: "",
                                },
                            };
                        }
                        this.setState(newState);
                    })
                    .catch((err) => {
                        this.setState({
                            errMsg: {
                                type: "danger",
                                message: err.message,
                            },
                        });
                        console.log(err);
                    });
            }
        } else {
            this.setState({
                errors,
            });
        }
    };

    closeModal = () => {
        this.setState(
            {
                formData: {},
            },
            () => {
                this.props.toggleModal(false);
            }
        );
    };

    hideErrMsg = () => {
        this.setState({
            errMsg: {
                show: false,
                type: "",
                message: "",
            },
        });
    };

    render() {
        const { formData, errors, errMsg, submit } = this.state;
        const { show } = this.props;

        return (
            <>
                <Modal
                    className="small"
                    show={show}
                    onHide={() => this.closeModal()}
                >
                    <Modal.Header>
                        <span
                            className="close"
                            onClick={() => this.closeModal()}
                        >
                            x
                        </span>
                    </Modal.Header>
                    <Modal.Body>
                        <div className="modal-content-wrapper">
                            <h2>
                                Want to be one of the first to play in our app?
                            </h2>
                            <p>
                                As stated before, this app is all about you!
                                People that choose to participate in focus
                                groups essentially guide the development of our
                                app. You help choose features and make sure it
                                is intuitive and valuable to the community.
                            </p>
                            <p>
                                After signing up, we will send you an email with
                                the next steps.{" "}
                            </p>
                            <p>Thanks for your support!</p>
                        </div>
                        <div className="form-wrapper">
                            {errMsg && errMsg.show && (
                                <Alert
                                    variant={errMsg.type}
                                    onClose={() => this.hideErrMsg()}
                                    dismissible
                                >
                                    {errMsg.message}
                                </Alert>
                            )}
                            <Row>
                                <Col>
                                    <div className="form-group">
                                        <label>First Name</label>
                                        <input
                                            type="text"
                                            name="fname"
                                            onChange={(e) => {
                                                this.onChange(e);
                                            }}
                                            className={
                                                errors.fname
                                                    ? "form-control error"
                                                    : "form-control"
                                            }
                                            value={formData.fname}
                                        />
                                    </div>
                                </Col>
                                <Col>
                                    <div className="form-group">
                                        <label>Last Name </label>
                                        <input
                                            type="text"
                                            name="lname"
                                            onChange={(e) => {
                                                this.onChange(e);
                                            }}
                                            className={
                                                errors.lname
                                                    ? "form-control error"
                                                    : "form-control"
                                            }
                                            value={formData.lname}
                                        />
                                    </div>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <div className="form-group">
                                        <label>Email </label>
                                        <input
                                            type="email"
                                            name="email"
                                            onChange={(e) => {
                                                this.onChange(e);
                                            }}
                                            className={
                                                errors.email
                                                    ? "form-control error"
                                                    : "form-control"
                                            }
                                            value={formData.email}
                                        />
                                    </div>
                                </Col>
                                <Col>
                                    <div className="form-group">
                                        <label>Phone Number </label>
                                        <input
                                            type="text"
                                            name="phone"
                                            onChange={(e) => {
                                                this.onChange(e);
                                            }}
                                            className={
                                                errors.phone
                                                    ? "form-control error"
                                                    : "form-control"
                                            }
                                            value={formData.phone}
                                        />
                                    </div>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <div className="form-group">
                                        <label>Preferred Social Platform</label>
                                        <select
                                            name="social_plateform"
                                            className={
                                                errors.social_plateform
                                                    ? "form-control error"
                                                    : "form-control"
                                            }
                                            onChange={(e) => {
                                                this.onChange(e);
                                            }}
                                        >
                                            <option value="">Select</option>
                                            <option
                                                selected={
                                                    formData.social_plateform ===
                                                    "Facebook"
                                                        ? "selected"
                                                        : ""
                                                }
                                                value="Facebook"
                                            >
                                                Facebook
                                            </option>
                                            <option
                                                selected={
                                                    formData.social_plateform ===
                                                    "Twitter"
                                                        ? "selected"
                                                        : ""
                                                }
                                                value="Twitter"
                                            >
                                                Twitter
                                            </option>
                                            <option
                                                selected={
                                                    formData.social_plateform ===
                                                    "Linkedin"
                                                        ? "selected"
                                                        : ""
                                                }
                                                value="Linkedin"
                                            >
                                                Linkedin
                                            </option>
                                            <option
                                                selected={
                                                    formData.social_plateform ===
                                                    "Instagram"
                                                        ? "selected"
                                                        : ""
                                                }
                                                value="Instagram"
                                            >
                                                Instagram
                                            </option>
                                        </select>
                                    </div>
                                </Col>
                                <Col>
                                    <div className="form-group">
                                        <label>Handle or URL </label>
                                        <input
                                            type="text"
                                            name="handle_url"
                                            onChange={(e) => {
                                                this.onChange(e);
                                            }}
                                            className={
                                                errors.handle_url
                                                    ? "form-control error"
                                                    : "form-control"
                                            }
                                            value={formData.handle_url}
                                        />
                                    </div>
                                </Col>
                            </Row>
                            <div className="form-group btn-wrapper">
                                <Button
                                    className="btn btn-medium solid"
                                    onClick={() => this.submitForm()}
                                >
                                    {submit && (
                                        <i className="fas fa-spinner fa-spin" />
                                    )}
                                    {!submit && <span>SUBMIT</span>}
                                </Button>
                            </div>
                        </div>
                    </Modal.Body>
                    {/* <Modal.Footer className="justify-content-start">
                        <Button
                            className="btn btn-medium solid"
                            onClick={() => this.submitForm()}
                        >
                            <span>SUBMIT</span>
                        </Button>
                    </Modal.Footer> */}
                </Modal>
            </>
        );
    }
}

export default FocusGroup;
