import React, { useState } from "react";
import { connect } from "react-redux";
import { Row, Col, Container } from "react-bootstrap";

import {
    FocusGroup,
    BecomePartner,
    WantInModal,
    DonateNow,
} from "../../Components/Modals";

import { Logo } from "../../Constants/Images";

const Header = (props) => {
    const [focusModal, setFocusModal] = useState(false);
    const [becomePartnerModal, setBecomePartnerModal] = useState(false);
    const [wantInModal, setWantInModal] = useState(false);
    const [donateNowModal, setDonateNowModal] = useState(false);

    const { settings } = props;

    return (
        <React.Fragment>
            <div id="loader" className="loader">
                <div className="loader-logo">
                    <img src={Logo} alt="" />
                </div>
            </div>
            <div className="site-header">
                <Container>
                    <Row>
                        <Col md={3} sm={3} xs={6}>
                            <div className="logo">
                                <img src={Logo} alt="" />
                            </div>
                        </Col>
                        <Col md={9} sm={9} xs={6}>
                            <div className="hamberger">
                                <span />
                                <span />
                                <span />
                            </div>
                            <div className="navigation-wrapper text-right">
                                <div className="navigation">
                                    <ul>
                                        <li>
                                            <span
                                                className="as-link"
                                                onClick={() => {
                                                    setFocusModal(true);
                                                }}
                                            >
                                                Participate In Focus Groups
                                            </span>
                                        </li>
                                        <li>
                                            <span
                                                className="as-link"
                                                onClick={() => {
                                                    setBecomePartnerModal(true);
                                                }}
                                            >
                                                Become A Partner
                                            </span>
                                        </li>
                                        <li>
                                            <span className="as-link">
                                                Meet The Founder
                                            </span>
                                        </li>
                                        <li>
                                            <span
                                                className="btn btn-small"
                                                onClick={() => {
                                                    setWantInModal(true);
                                                }}
                                            >
                                                I WANT IN
                                            </span>
                                        </li>
                                    </ul>
                                </div>
                                {settings.stripe_pk_key && (
                                    <div className="donation-text">
                                        <p>
                                            We are self funded and black owned.{" "}
                                            <span
                                                className="donate-btn"
                                                onClick={() => {
                                                    setDonateNowModal(true);
                                                }}
                                            >
                                                {" "}
                                                Donate
                                            </span>
                                        </p>
                                    </div>
                                )}
                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>

            {focusModal && (
                <FocusGroup
                    show={focusModal}
                    toggleModal={(status) => {
                        setFocusModal(status);
                    }}
                />
            )}
            {becomePartnerModal && (
                <BecomePartner
                    show={becomePartnerModal}
                    toggleModal={(status) => {
                        setBecomePartnerModal(status);
                    }}
                />
            )}
            {wantInModal && (
                <WantInModal
                    show={wantInModal}
                    toggleModal={(status) => {
                        setWantInModal(status);
                    }}
                />
            )}
            {donateNowModal && (
                <DonateNow
                    show={donateNowModal}
                    toggleModal={(status) => {
                        setDonateNowModal(status);
                    }}
                />
            )}
        </React.Fragment>
    );
};

const mapStateProps = (state) => ({
    settings: state.settings,
});

export default connect(mapStateProps, {})(Header);
