import React from "react";
import { connect } from "react-redux";
import { Row, Col, Container } from "react-bootstrap";
import Counter from "../../Components/Countdown";

const Footer = (props) => {
    const checkDifferent = (timeStamp) => {
        const current = Date.now();
        const difference = timeStamp > current ? timeStamp - current : 0;

        return current + difference;
    };

    const { countdown_end } = props.settings;
    const dateDifferent = checkDifferent(countdown_end * 1000);

    return (
        <div className="footer">
            <Container>
                <Row>
                    <Col>
                        <div className="count-downtext">
                            <span>The Countdown Has Started</span>
                            <Counter
                                date={dateDifferent}
                                onComplete={() => {
                                    return (
                                        <div className="timer-wrapper">
                                            <span> Event already started!</span>
                                        </div>
                                    );
                                }}
                            />
                        </div>
                    </Col>
                    <Col>
                        <div className="store-linktext">
                            <p>
                                Outside of donations, we use our store to fund
                                the development.
                            </p>
                            <p>
                                Check out our{" "}
                                <a
                                    href="https://shop.tryingtobuild.com/"
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    Apparel and Accessories
                                </a>
                            </p>
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
    );
};

const mapStateProps = (state) => {
    return { settings: state.settings };
};

export default connect(mapStateProps, {})(Footer);
