import React from "react";
import Countdown from "react-countdown-now";
// import DateCountdown from "react-date-countdown-timer";

const Counter = (props) => {
    const { date, onComplete } = props;

    const renderer = ({ days, hours, minutes, seconds, completed }) => {
        if (completed) {
            return onComplete();
        } else {
            // console.log("Month :", months);
            const daysText = days > 1 ? "Days" : "Day";
            const HoursText = hours > 1 ? "Hours" : "Day";
            const MinutesText = minutes > 1 ? "Minutes" : "Day";
            const SecondsText = seconds > 1 ? "Seconds" : "Second";

            return (
                <div className="timer-wrapper">
                    {days > 0 && <span>{`${days} ${daysText}`}</span>}
                    {hours > 0 && <span>{`${hours} ${HoursText}`}</span>}
                    {minutes > 0 && <span>{`${minutes} ${MinutesText}`}</span>}
                    {seconds > 0 && <span>{`${seconds} ${SecondsText}`}</span>}
                </div>
            );
        }
    };

    return <Countdown date={date} renderer={renderer} />;
};

export default Counter;
