import { GET_SETTINGS } from "../Utils/types";

import API from "../Utils/Api";

// Admin User Actions
export const getSettingsDispatch = (settings) => ({
    type: GET_SETTINGS,
    settings,
});

export const getSiteSettings = () => (dispatch) =>
    API.getSettings().then((res) => {
        const settings = res.settings;
        if (settings) {
            dispatch(getSettingsDispatch(settings));
        }
        return res;
    });
