import React, { Component } from "react";
import { connect } from "react-redux";
import Routes from "./Routes";

import { getSiteSettings } from "./Actions/getSettings";

class App extends Component {
    componentWillMount = () => {
        this.props
            .getSiteSettings()
            .then((res) => {
                console.log("Res", res);
            })
            .catch((err) => {
                console.log("Err", err);
            });
    };
    render() {
        return <Routes />;
    }
}

export default connect(null, { getSiteSettings })(App);
